<template>
  <div id="app"
       v-loading="loading">
    <!-- <img src="./assets/logo.png"> -->
    <h3 class="title">
      电子与信息工程学院签到查询
    </h3>
    <div class="select-item">
      <el-row>
        <el-col :span="24">
          <span>班级：</span>
          <el-input style="width:60%;"
                    placeholder="请输入班级"
                    v-model="classInput"
                    clearable>
          </el-input>
        </el-col>
      </el-row>
    </div>
    <div class="select-item">
      <el-row>
        <el-col :span="24">
          日期：
          <el-date-picker style="width:60%;"
                          v-model="dateValue"
                          type="date"
                          placeholder="选择日期">
          </el-date-picker>
        </el-col>
      </el-row>
    </div>
    <div class="select-item">
      <el-row>
        <el-col :span="24">
          课序：
          <el-select v-model="indexValue"
                     style="width:60%;"
                     placeholder="请选择课序">
            <el-option v-for="item in indexOptions"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
        </el-col>
      </el-row>
    </div>
    <el-button @click="query()"
               type="success"
               style="margin-top:30px;width:50%;margin-bottom:30px;"
               round>查询</el-button>
    <el-row>
      <el-tag type="success"
              style="margin-right:10px;">班级总人数：{{classSum}}</el-tag>
      <el-tag type="danger">未签到人数：{{noSign}}</el-tag>
    </el-row>
    <el-table :data="signData"
              v-if="signData.length > 0"
              style="width: 90%;margin:0 auto;">
      <el-table-column prop="number"
                       label="学号"
                       width="120">
      </el-table-column>
      <el-table-column prop="name"
                       label="姓名">
      </el-table-column>
      <el-table-column prop="state"
                       label="状态">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
export default {
  name: 'app',
  components: {
    // HelloWorld
  },
  data () {
    return {
      indexOptions: [{
        value: 1,
        label: '第一节'
      },
      {
        value: 2,
        label: '第二节'
      },
      {
        value: 3,
        label: '第三节'
      },
      {
        value: 4,
        label: '第四节'
      },
      {
        value: 5,
        label: '第五节'
      }],
      classInput: '',
      dateValue: '',
      indexValue: '',
      classSum: 0,
      noSign: 0,
      signData: [],
      loading: false
    }
  },
  methods: {
    getAllGrades () {
      // this.axios.get('/api/sign/getOptions').then((response) => {
      //   this.gradeOptions = response.data.
      // }).catch((response) => {

      // })
    },
    classCheck () {
      if (this.classInput === '') {
        return false
      }
      return true
    },
    query () {
      if (!this.classCheck()) {
        this.classError('班级不能为空')
        return
      }
      if (this.dateValue === '') {
        this.classError('未选择日期')
        return
      }
      if (this.indexValue === '') {
        this.classError('未选择课序号')
        return
      }
      this.loading = true
      let data = { 'iClass': this.classInput, 'date': this.dateFormat('YYYY-mm-dd', this.dateValue), 'index': this.indexValue }
      this.axios.post('/lntuplus/sign/get', data).then((response) => {
        this.signData = response.data.list
        this.classSum = response.data.sum
        if (this.classSum === 0) {
          this.classError('班级填写错误')
          return
        }
        this.noSign = this.signData.length
        for (let i = 0; i < this.signData.length; i++) {
          this.signData[i].state = '未签到'
        }
      }).catch((response) => {
      })
      this.loading = false
    },
    dateFormat (fmt, date) {
      let ret
      let opt = {
        'Y+': date.getFullYear().toString(),
        'm+': (date.getMonth() + 1).toString(),
        'd+': date.getDate().toString(),
        'H+': date.getHours().toString(),
        'M+': date.getMinutes().toString(),
        'S+': date.getSeconds().toString()
      }
      for (let k in opt) {
        ret = new RegExp('(' + k + ')').exec(fmt)
        if (ret) {
          fmt = fmt.replace(ret[1], (ret[1].length === 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, '0')))
        }
      }
      return fmt
    },
    classError (str) {
      this.$message.error(str)
    }

  },
  watch: {
    dateValue: function (val) {
    }
  },
  created () {
  }
}
</script>

